import { useDispatch, useSelector } from "react-redux";

import { Box, Container } from "@mui/system";
import { useEffect, useState } from "react";
import Input from "@mui/material/Input";
import InputAdornment from "@mui/material/InputAdornment";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";

import Select from "@mui/material/Select";
import {
  getBrandsAction,
  getVehiclesAction,
  searchFormChange,
} from "../../../redux/actions/VehicleActions";
import Autocomplete from "@mui/material/Autocomplete";
import TextField from "@mui/material/TextField";
import VehicleCard from "./VehicleCard";
import { useNavigate } from "react-router-dom";
import CustomContainer from "../../components/CustomContainer";
import SearchVehicle from "./SearchVehicle";
function Home() {
  const state = useSelector((state) => state);
  const vehicleStore = useSelector((state) => state.vehicleStore);
  const searchForm = useSelector((state) => state.searchForm);
  const brands = useSelector((state) => state.brandStore.data);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(getVehiclesAction);
    dispatch(getBrandsAction);
  }, []);

  useEffect(() => {
    console.log("vehicleStore", vehicleStore);
  }, [vehicleStore]);

  const onInputChange = (name) => {
    return (e) => {
      const val = e.target.value || e.target.textContent;
      dispatch(searchFormChange({ [name]: val }));
    };
  };

  return (
    <CustomContainer>
      <Box
        sx={{
          backgroundColor: "#362b358c",
          border: "2px solid purple",
          color: "white",

          "& label": {
            color: "#ffb7ff",
          },

          "& .MuiInputBase-input": {
            color: "white",
          },
          "& p": {
            color: "#ffb7ff",
          },
        }}
      >
        <SearchVehicle />
      </Box>

      <Box
        sx={{
          display: "flex",
          flexWrap: "wrap",
          "& > *": {
            m: 1,
            width: "30%",
          },
        }}
      >
        {vehicleStore?.data?.map((vehicle, ind) => {
          return <VehicleCard key={ind} vehicle={vehicle} />;
        })}
      </Box>
    </CustomContainer>
  );
}

export default Home;
